.backButton {
  cursor: pointer;
  width: 80px;
  text-align: center;
  font-weight: bold;
  padding: 8px 15px;
  margin-bottom: 15px;
  box-shadow: 1px 2px 2px 0 rgb(0, 0, 0/0.5);
  transition: box-shadow 250ms linear;
  border-radius: 10px;
  border: 1px solid #2196f3;
}

.backButton:hover {
  background-color: #2196f3;
  color: white;
}

.reviews {
  display: inline-block;
  padding: 8px 15px;
  border-radius: 10px;
  border: 1px solid #2196f3;
  margin-right: 30px;
  font-weight: bold;
}

.cast {
  display: inline-block;
  padding: 8px 28px;
  border-radius: 10px;
  border: 1px solid #2196f3;
  font-weight: bold;
}

.reviews:hover,
.cast:hover {
  background-color: #2196f3;
  color: white;
}

.divMovieDetailsPage {
  width: 1000px;
  margin-left: auto;
  margin-right: auto;
  background-color: aliceblue;
  padding: 20px;
}
.divGenres {
  display: flex;
  gap: 10px;
}
.divMovie {
  display: flex;
  gap: 30px;
}
